*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.heading{
  text-align: center;
  margin-top: 20px;
}
#the{
  writing-mode: vertical-rl;
  transform: rotate(-180deg);
  font-size: 25px;
  margin-right: 10px;
}
#siren{
  font-size: 50px;
  font-weight: bold;
}
.navbar{
  display: flex;
  justify-content: space-evenly;
  margin-top: 40px;
  height: 45px;
  width: 90%;
  margin-left: 70px;
}
.navlink{
  text-decoration: none;
  color: black;
  font-size: 20px;
  transition-property: color;
}
.navlink:hover{
  color: goldenrod;
}
hr{
  width: 85%;
  height: 3px;
  background-color: rgb(226, 226, 226);
  margin-left: 100px;
  border: none;
}
#homeimg1{
  position: relative;
  top: 30px;
  left: 190px;
  width: 600px;
  height: 420px;
  border-radius: 5px;
}
#homeimg2{
  position: relative;
  top: -190px;
  left: 200px;
  width: 350px;
  height: 200px;
  border-radius: 5px;
}
#homeimg3{
  position: relative;
  top: 30px;
  left: -150px;
  width: 350px;
  height: 210px;
  border-radius: 5px;
}
#homecompo2{
  font-size: 30px;
  font-weight: bold;
  margin-top: 130px;
  margin-left: 150px;
}
#homeimg4{
  height: 200px;
  width: 300px;
  margin-top: 40px;
  margin-left: 180px;
}
#baseContainer{
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 60px;
}
#block1{
  display: flex;
  flex-direction: column;
  width: 150%;
  margin-left: 130px;
  margin-top: 20px;
  font-size: 15px;
  line-height: 20px;
}
#block2{
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 20px;
  width: 100%;
  margin-top: 20px;
  float: right;
}
.container1{
  display: flex;
  flex-direction: row;
  margin-top: 30px;
  width: 90%;
}
.container2{
  display: flex;
  flex-direction: row;
  margin-top: 50px;
  width: 70%;
}
hr{
  margin-left: -8px;
  margin-top: 20px;
  width: 100%;
}
#hrhead{
  width: 10%;
  margin-left: 5px;
  margin-top: 8px;
  background-color: rgb(240, 107, 107);
}
.header{
  margin-top: 20px;
  font-size: 25px;
  font-weight: bold;

}
.contentContainer{
  margin-left: 15px;
} 
.image{
  width: 250px;
  height: 170px;
  box-shadow: 1px 1px 5px 0 gray;
}
.imageCorner{
  width: 120px;
  height: 100px;
  box-shadow: 1px 1px 5px 0 gray;
}
.highlights{
  font-weight: 500;
}
.highlightsCorner{
  font-weight: 500;

}
.content{
  font-weight: 400;
}
.contentCorner{
  font-weight: 400;
}
.adbox{
  margin-top: 50px;
  height: 40em;
  width: 70%;
}